// shared.scss

$primary-font-fam:  sans-serif;

$primary-color: hsl(0, 0%, 96%);
// $primary-dark-color: hsl(210, 0%, 78%);
$secondary-color: hsl(194, 74%, 20%);
$secondary-dark-color: hsl(194, 74%, 10%);
$accent1-color: #da372f;
// $accent2-color: $secondary-dark-color;

//  2em;        2/sqrt1
$FSH1: calc(4vw * 1.5);
//  1.414em;    2/sqrt2
$FSH2: calc(3vw * 1.5);
//  1.155em;    2/sqrt3
$FSH3: calc(2.5vw * 1.5);
//  1em;        2/sqrt4
$FSH4: calc(2vw * 1.5);
//  0.894em;    2/sqrt5
$FSH5: calc(1.5vw * 1.5);
//  0.816em;    2/sqrt6
$FSH6: calc(1vw * 1.5);
// 2/sqrt7
$FSH7: calc(0.756em * 1.5);